/*=== Custom Scrollbar ===*/
::-webkit-scrollbar {
    width: 8px;
    background: rgba(228, 228, 228, 0.8);
}

::-webkit-scrollbar-thumb {
    background: rgba(73, 73, 73, 0.8);
}

::-webkit-scrollbar-track {
    border-radius: 2px;
}

