.team-member {
    max-width: 980px;
    margin: 0 auto;
    padding: 50px 0;
    text-align: center;
}

.team-member ul {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    margin-top: 50px;
}

.team-member ul li {
    display: flex;
    flex-direction: column;
}

.team-member ul li img {
    max-width: 100%;
    border-radius:  5px 5px 0 0;
}

.team-member ul li footer {
    flex: 1 1;
    background: #292F36;
    padding: 15px 20px;
    text-align: left;
    border-radius: 0 0 5px 5px;
}

.team-member ul li footer strong {
    font-size: 16px;
    color: #333;
}

.team-member ul li footer p {
    font-size: 14px;
    line-height: 20px;
    color: #999;
    margin-top: 5px;
}

.team-member ul li .buttons {
    margin-top: 10px;
    display: grid;
    grid-template: repeat(2, 1fr);
    grid-gap: 10px;
}

.team-member ul li .buttons button {
    height: 50px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.05);
    border: 0;
    border-radius: 4px;
    background: #FFF;
    cursor: pointer;
}

.team-member ul li .buttons button:hover img {
    -webkit-transform:  translateY(-5px);
            transform:  translateY(-5px);
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}

.team-member .empty {
    font-size: 32px;
    color: #999;
    font-weight: bold;
    margin-top: 300px;
}

.team-member span {
    font-size: 13px;
}
.footer-bottom {
    height: 150px;
    background-color: #171717;
    text-align: center;
}

.footer-bottom .copyright span {
    margin-top: 75px;
    font-size: 14px;
    color: #737f8a;
    display: inline-block;
}

.footer-bottom .copyright span::after {
    content: "|";
    display: inline-block;
    padding: 0 1rem 0 1.2rem;
    color: rgba(255, 255, 255, 0.1);
}

.footer-bottom .copyright span:last-child::after {
    display: none;
}

